@media (max-width: 800px) {
    .form-container form {
        flex-direction: column !important;
    }
    .left {
        width: 100% !important;
    }
    .right {
        width: 100% !important;
    }
    #id-create-btn {
        width: 100% !important;
    }
}

.form-container {
    display: flex;
    width: 100%;
    flex-direction: column;
    color: black;
    align-items: center;
    margin: auto;
}

.form-container form {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-evenly;
    flex-direction: row;
}

.left {
    display: flex;
    width: 45%;
    flex-direction: column;
}

.right {
    display: flex;
    width: 45%;
    flex-direction: column;
}

.form-container form input {
    height: 30px;
}

#id-create-btn {
    display: flex;
    width: 50%;
    margin: 50px auto;
    padding: 10px;
    background-color: #303030;
    border: solid 1px #888;
    justify-content: center;
    outline: none;
    color: white;
}

#id-create-btn:hover {
    cursor: pointer;
    opacity: 0.5;
    transition: 0.5s;
}